/* eslint-disable array-callback-return */
/* eslint-disable jsx-a11y/img-redundant-alt */
import React, { useEffect, useMemo, useState, useRef } from "react"; // useRef 
import ReactDOM from "react-dom"; // ReactDOM 
import {
  IIDiv,
  IIdivImage,
  InputKeyword,
  SearchDiv,
  SearchDivDiv,
  SearchSpan,
  SdivTotal,
  ResetButton,
  LoadingAni,
  LoadingText,
  StyledButton,
  IdentitySelectBox,
  ButtonText,
  FilterButton,
  RecommendationDiv,
  RecommendationContainer,
} from "./IdentityInfoStyle";
import Identity from "./Identity";
import { PaginationButtons } from "../components/pagenation/PagenationButton";
import ItemComponents from "../components/Identity/ItemComponents";

import { unstable_batchedUpdates } from "react-dom";

export default function IdentityInfo() {
  const versionSync = "sync4";
  const rows = 2;
  const columns = 6;

  const [allIdentityData, setAllIdentityData] = useState([]);
  const [clickedIdentityData, setClickedIdentityData] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [syncStates, setSyncStates] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [buttonData, setButtonData] = useState([]);

  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  const [selectedGrade, setSelectedGrade] = useState("");

  const [suggestions, setSuggestions] = useState([]);
  const [filterTerm, setFilterTerm] = useState("");

  const [recommendations, setRecommendations] = useState([]);

  const inputRef = useRef(null);
  const [position, setPosition] = useState({ top: 0, left: 0 });

  const [autoSuggest, setAutoSuggest] = useState(false); //   

  const portal = (
    <RecommendationContainer
      style={{
        top: `${position.top}px`,
        left: `${position.left}px`,
      }}
    >
      {recommendations.map((recommendation, index) => (
        <RecommendationDiv
          key={index}
          onClick={() => {
            setFilterTerm(recommendation);
            setRecommendations([]);
            setSearchTerm(recommendation);
          }}
        >
          {recommendation}
        </RecommendationDiv>
      ))}
    </RecommendationContainer>
  );

  //    useEffect
  useEffect(() => {
    if (inputRef.current) {
      const rect = inputRef.current.getBoundingClientRect();
      setPosition({
        top: rect.bottom + window.scrollY,
        left: rect.left + window.scrollX,
      });
    }
  }, [inputRef, searchTerm]);

  useEffect(() => {
    if (searchTerm !== "") {
      if (autoSuggest) {
        //  
        const newRecommendations = Array.from(
          new Set(
            allIdentityData
              .flatMap((identity) => identity.keyword)
              .filter((key) => key.includes(searchTerm))
          )
        );
        setRecommendations(newRecommendations);
      }
    } else {
      setRecommendations([]);
    }
  }, [searchTerm, allIdentityData, autoSuggest]); // useEffect   autoSuggest 

  useEffect(() => {
    if (searchTerm.length >= 1) {
      const newSuggestions = Array.from(
        new Set(
          allIdentityData
            .flatMap((identity) => identity.keyword)
            .filter((key) => key === searchTerm)
        )
      );
      setSuggestions(newSuggestions);
    } else {
      setSuggestions([]);
    }
  }, [searchTerm, allIdentityData]);

  const handleGradeClick = (grade) => {
    setSelectedGrade(grade);
    setCurrentPage(1);
  };

  const filteredItems = (filterTerm ? clickedIdentityData : allIdentityData) // filterTerm  
    .filter((identity) => {
      return selectedGrade === "" || identity.rank === selectedGrade;
    });

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = filteredItems.slice(indexOfFirstItem, indexOfLastItem);

  const handlePageClick = (pageNumber) => {
    if (pageNumber >= 1 && pageNumber <= totalPages) {
      setCurrentPage(pageNumber);
      window.scrollTo(0, 0);
    }
  };

  const calculateTotalPages = () => {
    let totalPages = Math.ceil(filteredItems.length / itemsPerPage);
    if (totalPages === 0) totalPages = 1;
    return totalPages;
  };

  const totalPages = calculateTotalPages();

  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/json/Identity/identitykeyword.json`)
      .then((response) => response.json())
      .then((data) => setButtonData(data));
  }, []);

  useEffect(() => {
    if (currentPage > totalPages) {
      setCurrentPage(totalPages);
    }
  }, [currentPage, totalPages]);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      let data = [];
      let initialSyncStates = {};
      let initialImageSrcs = {};

      for (let identity of Identity) {
        const response = await fetch(identity.path);
        const jsonData = await response.json();
        data = [...data, ...jsonData];
      }

      data.forEach((item) => {
        initialSyncStates[item.id] = versionSync;
        initialImageSrcs[item.id] = item.imgsrc;
      });

      data.sort((a, b) => b.id - a.id);

      setAllIdentityData(data);
      setClickedIdentityData(data);
      setSyncStates(initialSyncStates);
      setCurrentPage(1);
      setIsLoading(false);
    };
    fetchData();
  }, []);

  useEffect(() => {
    if (filterTerm !== "") {
      //    .
      const filteredData = allIdentityData.filter((identity) =>
        identity.keyword.some((key) => key === filterTerm)
      );
      setClickedIdentityData(filteredData);
    } else {
      setClickedIdentityData(allIdentityData);
    }
  }, [filterTerm, allIdentityData]);

  function resetAllFilters() {
    let initialSyncStates = {};

    allIdentityData.forEach((item) => {
      initialSyncStates[item.id] = versionSync;
    });

    setClickedIdentityData(allIdentityData);
    setSearchTerm("");
    setSyncStates(initialSyncStates);
    setCurrentPage(1);
  }

  const handleClick = (index) => {
    const clickedIdentity = Identity[index];
    if (!clickedIdentity) return;

    const newSearchTerm = clickedIdentity.name; //   

    // searchTerm filterTerm 
    setAutoSuggest(false);
    setSearchTerm(newSearchTerm);
    setFilterTerm(newSearchTerm); //    .
    setCurrentPage(1); //  1 

    fetch(clickedIdentity.path)
      .then((res) => res.json())
      .then((data) => {
        unstable_batchedUpdates(() => {
          data.sort((a, b) => b.id - a.id);

          let initialSyncStates = {};
          let initialImageSrcs = {};
          data.forEach((item) => {
            initialSyncStates[item.id] = versionSync; //       
            initialImageSrcs[item.id] = item.imgsrc; //       
          });

          setClickedIdentityData(data);
          setSyncStates(initialSyncStates);
        });
      })
      .catch((error) => console.error("error occurred: ", error));
  };

  const handleButtonClick = (desc) => {
    setSearchTerm(desc);
    setFilterTerm(desc);
    setAutoSuggest(false); //     autoSuggest false 
  };

  return (
    <>
      {isLoading ? (
        <>
          <LoadingAni></LoadingAni>
          <LoadingText>  ...</LoadingText>
        </>
      ) : (
        <>
          <IdentitySelectBox>
            {buttonData.map((button, index) => (
              <StyledButton
                key={index}
                isSelected={searchTerm === button.desc}
                onClick={() => handleButtonClick(button.desc)}
              >
                <img
                  src={`${process.env.PUBLIC_URL}/assets/images/etc/status/${button.img}`}
                  alt={button.title}
                />
                <ButtonText>{button.title}</ButtonText>
              </StyledButton>
            ))}
          </IdentitySelectBox>
          <SearchDiv>
            <SearchDivDiv></SearchDivDiv>
            <SearchDivDiv>
              <SearchSpan> : </SearchSpan>
              {[1, 2, 3].map((grade) => (
                <FilterButton
                  key={grade}
                  isSelected={selectedGrade === grade}
                  onClick={() => handleGradeClick(grade)}
                >
                  {grade}
                </FilterButton>
              ))}
              <FilterButton
                isSelected={selectedGrade === ""}
                onClick={() => setSelectedGrade("")}
              >
                
              </FilterButton>
            </SearchDivDiv>
            <SearchDivDiv></SearchDivDiv>
          </SearchDiv>
          <SearchDiv>
            <SearchDivDiv></SearchDivDiv>
            <SearchDivDiv>
              <SearchSpan>  : </SearchSpan>
              <InputKeyword
                ref={inputRef}
                type="text"
                placeholder=" "
                value={searchTerm}
                onChange={(e) => {
                  setSearchTerm(e.target.value);
                  setAutoSuggest(true); //   autoSuggest true 
                }}
              />

              {recommendations.length > 0 &&
                autoSuggest && //  
                ReactDOM.createPortal(portal, document.body)}
              <ResetButton onClick={resetAllFilters}></ResetButton>
            </SearchDivDiv>
            <SearchDivDiv></SearchDivDiv>
          </SearchDiv>
          <IIDiv>
            {Array.from({ length: rows * columns }, (_, index) => {
              const identity = Identity[index];
              return (
                <IIdivImage
                  key={index}
                  src={`${process.env.PUBLIC_URL}/assets/images/etc/portrait/${index}.webp`}
                  alt={identity ? identity.name : `Image ${index}`}
                  onClick={() => {
                    handleClick(index);
                  }}
                />
              );
            })}
          </IIDiv>
          <PaginationButtons
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={handlePageClick}
          />
          <SdivTotal>
            {currentItems.map((item, index) => (
              <ItemComponents
                key={index}
                index={index}
                item={item}
                searchTerm={searchTerm}
                setSearchTerm={setSearchTerm} //     
                setFilterTerm={setFilterTerm} //   
              />
            ))}
          </SdivTotal>
          <PaginationButtons
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={handlePageClick}
          />
        </>
      )}
    </>
  );
}
